/* Reset và base styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    "Helvetica Neue", Arial, sans-serif;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  min-height: 100vh;
  padding: 20px;
  color: #333;
}

/* Container chính */
.container {
  max-width: 1400px;
  margin: 0 auto;
}

/* Phần thêm todo */
.addtodo {
  background: white;
  border-radius: 16px;
  padding: 30px;
  margin-bottom: 30px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.addtodo h1 {
  color: #2c3e50;
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 25px;
  text-align: center;
  position: relative;
}

.addtodo h1::after {
  content: "";
  position: absolute;
  bottom: -8px;
  left: 50%;
  transform: translateX(-50%);
  width: 60px;
  height: 3px;
  background: linear-gradient(90deg, #667eea, #764ba2);
  border-radius: 2px;
}

/* Form styles */
.form-group {
  margin-bottom: 20px;
}

.form-label {
  display: block;
  font-weight: 600;
  margin-bottom: 8px;
  color: #34495e;
  font-size: 0.95rem;
}

.form-control {
  width: 100%;
  padding: 12px 16px;
  border: 2px solid #e1e8ed;
  border-radius: 8px;
  font-size: 0.95rem;
  transition: all 0.3s ease;
  background: #fafbfc;
}

.form-control:focus {
  outline: none;
  border-color: #667eea;
  background: white;
  box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);
}

/* Button styles */
.btn-add {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border: none;
  padding: 14px 32px;
  border-radius: 8px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  box-shadow: 0 4px 15px rgba(102, 126, 234, 0.3);
}

.btn-add:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(102, 126, 234, 0.4);
}

.btn-add:active {
  transform: translateY(0);
}

/* Todo list container */
.todo-list {
  background: white;
  border-radius: 16px;
  padding: 30px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.todo-list h1 {
  color: #2c3e50;
  font-size: 1.8rem;
  font-weight: 700;
  margin-bottom: 25px;
  position: relative;
  padding-bottom: 15px;
}

.todo-list h1::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 50px;
  height: 3px;
  background: linear-gradient(90deg, #667eea, #764ba2);
  border-radius: 2px;
}

/* Todo list pending header */
.todo-list-pending {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 25px;
  flex-wrap: wrap;
  gap: 15px;
}

/* Table styles */
table {
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 40px;
  background: white;
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.08);
}

th {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  padding: 16px 12px;
  text-align: left;
  font-weight: 600;
  font-size: 0.9rem;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

td {
  padding: 16px 12px;
  border-bottom: 1px solid #f1f3f5;
  vertical-align: middle;
  font-size: 0.9rem;
}

.todo-item {
  transition: all 0.3s ease;
}

.todo-item:hover {
  transform: translateY(-1px);
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
}

/* Button styles trong table */
.btn {
  padding: 8px 16px;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-size: 0.85rem;
  font-weight: 500;
  margin: 0 4px;
  transition: all 0.3s ease;
  text-transform: capitalize;
}

.btn-primary {
  background: linear-gradient(135deg, #3b82f6, #1e40af);
  color: white;
  box-shadow: 0 2px 8px rgba(59, 130, 246, 0.3);
}

.btn-primary:hover {
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(59, 130, 246, 0.4);
}

.btn-danger {
  background: linear-gradient(135deg, #ef4444, #dc2626);
  color: white;
  box-shadow: 0 2px 8px rgba(239, 68, 68, 0.3);
}

.btn-danger:hover {
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(239, 68, 68, 0.4);
}

/* Text center utility */
.text-center {
  text-align: center;
  color: #6c757d;
  font-style: italic;
  padding: 30px !important;
  background: #f8f9fa;
}

/* Responsive design */
@media (max-width: 768px) {
  body {
    padding: 10px;
  }

  .addtodo,
  .todo-list {
    padding: 20px;
    margin-bottom: 20px;
  }

  .addtodo h1,
  .todo-list h1 {
    font-size: 1.5rem;
  }

  .todo-list-pending {
    flex-direction: column;
    align-items: stretch;
  }

  .todo-list-pending > div {
    display: flex;
    gap: 10px;
    flex-wrap: wrap;
  }

  table {
    font-size: 0.8rem;
  }

  th,
  td {
    padding: 12px 8px;
  }

  .btn {
    padding: 6px 12px;
    font-size: 0.8rem;
    margin: 2px;
  }

  /* Stack buttons vertically on mobile */
  td:nth-child(6),
  td:nth-child(7) {
    text-align: center;
  }
}

@media (max-width: 480px) {
  .addtodo h1,
  .todo-list h1 {
    font-size: 1.3rem;
  }

  table {
    display: block;
    overflow-x: auto;
    white-space: nowrap;
  }

  .btn {
    display: block;
    width: 100%;
    margin: 2px 0;
  }
}

/* Custom Ant Design overrides */
.ant-select {
  min-width: 120px;
}

.ant-picker {
  width: 100%;
}

.ant-tag {
  margin: 2px;
  border-radius: 4px;
  font-weight: 500;
}

/* Modal custom styles */
.ant-modal-header {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  border-radius: 8px 8px 0 0;
}

.ant-modal-title {
  color: white;
  font-weight: 600;
}

.ant-modal-close {
  color: white;
}

.ant-modal-close:hover {
  color: #f1f3f5;
}

/* Loading và animation effects */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.todo-item {
  animation: fadeIn 0.3s ease-in-out;
}

/* Deadline color indicators */
.todo-item[style*="red"] {
  background-color: #fee2e2 !important;
  border-left: 4px solid #ef4444;
}

.todo-item[style*="orange"] {
  background-color: #fef3c7 !important;
  border-left: 4px solid #f59e0b;
}

.todo-item[style*="lightgreen"] {
  background-color: #ecfdf5 !important;
  border-left: 4px solid #10b981;
}

/* Scrollbar styling */
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

::-webkit-scrollbar-track {
  background: #f1f3f5;
  border-radius: 4px;
}

::-webkit-scrollbar-thumb {
  background: linear-gradient(135deg, #667eea, #764ba2);
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(135deg, #5a6fd8, #6a4190);
}
